<template>
    <div class="container-chat">
        <div class="autorize">
            <div class="form_block">
                <form @submit.prevent="login">
                <h1>Вход</h1>
                <p v-if="serverErrr" class="error">{{ errData }}</p>
                <div class="input_block">
                    <input required v-model="username" type="username" placeholder="имя пользователя"/>
                </div>
                <div class="input_block">
                    <input required :type="!show_pass ? 'password' : 'text'" placeholder="пароль"  v-model="password"/>
                </div>
                <div class="check_block">
                    <div class="show_pass">
                        <input type="checkbox" id="show_pass" v-model="show_pass">
                        <label for="show_pass">Показать пароль</label>
                    </div>
                    <span><RouterLink to="restore">Забыли пароль?</RouterLink></span>
                </div>
                <div class="button_block">
                    <button type="submit" :disabled="!validate" :class="validate ? 'activ' : 'non_activ'" v-on:click="login">Вход</button>
                    <span><RouterLink to="register">Регистрация</RouterLink></span>
                </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>

import { post } from '@/api/netork';

export default{ 
    data(){
        return{
            username:"",
            password:"",
            coockie:"",
            serverErrr: false,
            errData:"",
            show_pass: false
        }
    },
    mounted(){
        localStorage.clear();
    },
    methods:{
        login: function(){
            let parametrs = {'username':this.username, 'password':this.password}; 
            post('/login', parametrs)          
                .then(() => {
                    this.$router.push('/chat');
                })
                .catch((err) => {
                    this.serverErrr = true;
                    this.errData = err.response.data;
                    console.log(this.errData);
                }) 
        }
    },
    computed:{
        validate(){
            if (this.username.length == 0 || this.password.length < 4){
                return false
            }
            return true
        }
    }
}

</script>
<style>
.error{    
    color: red;
    font-size: 20px;
    font-weight: bold;
}
</style>